header {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	max-width: 100vw;
	background-color: rgba(27, 22, 35);
	font-family: monospace;
	font-size: 1.4em;
	border-bottom: 2px solid rgb(49, 37, 58);
	z-index: 1000;
	padding: 15px 0;
	position: relative;
}

header:hover {
	box-shadow: 0 3px 12px rgba(49, 37, 58, 0.5);
}

header a {
	text-decoration: none;
}

.header-nav {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.logo {
	width: 60px;
	margin-right: 10px;
}

.header-nav ul {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-items: center;
	list-style: none;
}

.galaxies-links {
	padding: 12px 16px;
	font-size: 0.9em;
	border-radius: 7px;
	outline: 3px solid rgb(49, 37, 58);
	color: white;
	margin-right: 35px;
	text-shadow: 1px 3px 3px rgba(0, 0, 0, 0.5);
}

.logo-link {
	margin-left: 45px;
}

.logo-div {
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
}

.logo-div p {
	text-align: center;
	font-family: monospace;
	font-size: 1em;
	color: white;
	text-shadow: 0 0 5px rgb(220, 190, 233);
}

.logo-div p:first-child {
	margin-left: -3px;
}

.galaxies-links:hover {
	cursor: pointer;
	animation: background-gradient 0.2s ease-in-out 0s 1 normal forwards;
}

.login-link {
	margin-right: 45px;
}

.login-link img {
	width: 60px;
	border: 2px solid rgb(201 201 201);
	border-radius: 30px;
	padding: 3px;
}

.login-link img:hover {
	background-color: rgba(0, 0, 0, 0.3);
}

.menu-icon {
	display: none;
	width: 45px;
	margin-bottom: 1px;
}

.menu-icon:hover {
	cursor: pointer;
}

.profile-header {
	background-color: rgb(5, 3, 23);
	border-bottom: 2px solid rgb(87, 87, 87);
}

.profile-header:hover {
	box-shadow: 0 3px 12px rgb(56, 52, 52, 0.5);
}

.profile-galaxies-links {
	outline: 2px solid rgb(87, 87, 87);
}
