.xanh-mono-regular {
	font-family: 'Xanh Mono', monospace;
	font-weight: 400;
	font-style: normal;
}

.xanh-mono-regular-italic {
	font-family: 'Xanh Mono', monospace;
	font-weight: 400;
	font-style: italic;
}

.rubik-mono-one-regular {
	font-family: 'Rubik Mono One', monospace;
	font-weight: 400;
	font-style: normal;
}

.calistoga-regular {
	font-family: 'Calistoga', serif;
	font-weight: 400;
	font-style: normal;
}

.reddit-mono-regular {
	font-family: 'Reddit Mono', monospace;
	font-optical-sizing: auto;
	font-weight: 400;
	font-style: normal;
}

.inconsolata-regular {
	font-family: 'Inconsolata', monospace;
	font-optical-sizing: auto;
	font-weight: 400;
	font-style: normal;
	font-variation-settings: 'wdth' 100;
}
