@keyframes appear {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes squish {
	0% {
		transform: translateY(0);
		opacity: 1;
	}
	50% {
		transform: translateY(-7%);
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}

@keyframes levitate {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(7px);
	}
}

@keyframes galaxie-jump {
	from {
		transform: translateY(0px);
	}
	to {
		transform: translateY(-15px);
	}
}

@keyframes launch {
	from {
		opacity: 0;
		transform: translate(0, 0);
	}
	to {
		opacity: 1;
		transform: translate(75%, -50%);
	}
}
